import Vue from 'vue'
import App from './App.vue'
import router from './router'
import VueRouter from 'vue-router'
import store from './store'
import {Button, NavBar,Form,Field,Toast,Tabbar, TabbarItem,Swipe, SwipeItem,GoodsAction, GoodsActionIcon, GoodsActionButton,Icon,Stepper,SwipeCell,Checkbox, CheckboxGroup,SubmitBar,Empty,AddressEdit ,AddressList,ActionSheet,Tab, Tabs,Cell,
  CellGroup,Uploader,Dialog  } from 'vant'
import axios from 'axios'
import VueAxios from 'vue-axios'

// import 'vant/lib/index.css';
import 'lib-flexible';

Vue.config.productionTip = false

const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function (location) {
  return originalPush.call(this, location).catch(err => err);
}


// Vue.use(Vant);
Vue.use(VueAxios,axios)

Vue.use(Button)
   .use(NavBar)
   .use(Form)
   .use(Field)
   .use(Toast)
   .use(Tabbar)
   .use(TabbarItem)
   .use(Swipe)
   .use(SwipeItem)
   .use(GoodsAction)
   .use(GoodsActionIcon)
   .use(GoodsActionButton)
   .use(Icon)
   .use(Stepper)
   .use(SwipeCell)
   .use(Checkbox)
   .use(CheckboxGroup)
   .use(SubmitBar)
   .use(Empty )
   .use(AddressEdit)
   .use(AddressList )
   .use(ActionSheet )
   .use(Tab )
   .use(Tabs)
   .use(Cell)
   .use(CellGroup)
   .use(Uploader)
   .use(Dialog)
   

   


//配置请求基础路径
axios.defaults.baseURL = 'http://www.kangliuyong.com:10002'

//配置post请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';


//发起请求之前处理
axios.interceptors.request.use(config => {
  // 

  if (config.method == 'post') {
    let paramsString = '';
    //post请求参数序列化，转成一个字符串
    for (let key in config.data) {
      paramsString += key + '=' + config.data[key] + '&'
    }

    // 

    config.data = paramsString.slice(0, -1);

    // 
  }

  return config;
})

//添加appkey属性保存appkey
Vue.prototype.appkey = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
