export const routes = [
    {
        path:"/register",
        name:"Register",
        component:()=>import('../views/Register.vue')
    },
    {
        path:"/login",
        name:"Login",
        component:()=>import('../views/Login.vue')
    },
    {
        path:'/main',
        name:"Main",
        component:()=>import('../views/Main.vue'),
        children:[
            {
                path:'menu',
                name:"Menu",
                component:()=>import('../views/mainViews/Menu.vue')
            },
            {
                path:'order',
                name:"Order",
                component:()=>import('../views/mainViews/Order.vue')
            },
            {
                path:'shopcart',
                name:"Shopcart",
                component:()=>import('../views/mainViews/Shopcart.vue')
            },
            {
                path:'my',
                name:"My",
                component:()=>import('../views/mainViews/My.vue')
            },
        ]
    },
    {
        path:"/detail",
        name:"Detail",
        component:()=>import('../views/Detail.vue')
    },
    {
        path:"/address",
        name:"Address",
        component:()=>import('../views/Address.vue')
    },
    {
        path:"/addresslist",
        name:"Addresslist",
        component:()=>import('../views/Addresslist.vue')
    },
    {
        path:"/pay",
        name:"Pay",
        component:()=>import('../views/Pay.vue')
    },
    {
        path:"/account",
        name:"Account",
        component:()=>import('../views/Account.vue')
    },
    {
        path:"/collect",
        name:"Collect",
        component:()=>import('../views/Collect.vue')
    },
    {
        path:"/secure",
        name:"Secure",
        component:()=>import('../views/Secure.vue')
    },
    {
        path:"/changepsw",
        name:"Changepsw",
        component:()=>import('../views/Changepsw.vue')
    },
    {
        path:"/forget",
        name:"Forget",
        component:()=>import('../views/Forget.vue')
    },
    {
        path:"/next",
        name:"Next",
        component:()=>import('../views/Next.vue')
    },
    {
        path:"*",
        redirect:{
            name:"Menu",
        }
    }
]